@import '../../assets/css/variables';

.songs {
  padding: 25px 35px 100px 35px;
  width: 100%;
  position: relative;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  background-color: white;
  min-height: 90vh;

  .top, .list {
    width: 80%;
  }

  @media (max-width: 1100px) {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    padding: 10px;

    .top, .list {
      width: 100%;
    }
  }

  .top {

    h2 {
      margin: 0;
      color: #313131;
      font-weight: 800;
      font-size: 1.35em;
      padding-bottom: 5px;

    }
    p {
      margin: 0;
      color: #8e8e8e;
      font-size: .88em;
      font-weight: 300;
    }


    .right {
      position: absolute;
      top: 35px;
      right: 14%;
      display: grid;
      grid-template-columns: max-content repeat(2, 40px);
      justify-items: center;
      color: #a5a5a5;

      @media (max-width: 1100px) {
        right: 10px;
        top: 5px;
      }

      p {
        font-size: .86em
      }

      p, svg {
        cursor: pointer;
        transition: .3s;

        &:hover {
          color: $sidebarPurple;
        }
      }
    }
  }

  .list {
    @media (max-width: 1100px) {
      font-size: .92em;
    }

    .headline, article {
      display: grid;
      grid-template-columns: 2fr 1.5fr 0.5fr;

      @media (max-width: 1100px) {
        grid-template-columns: 2fr 1.4fr .35fr;
      }

    }

    .headline {
      color: #424242;

      @media (max-width: 350px) {
        display: none;
      }
    }

    article {
      margin-bottom: 5px;
      padding: 7px;
      border-bottom: 2px solid #f5f5f5;
      transition: .3s;
      cursor: pointer;
      opacity: .9;
      position: relative;

      @media (max-width: 350px) {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
        align-items: center;
        justify-items: center;
      }

      @media (max-width: 1100px) {
        padding: 4px;
      }

      &:hover {
        transform: scale(1.01);
        box-shadow: 0 2px 3px 1px rgba(238, 238, 238, 0.66);
        opacity: 1;
      }

      .track {
        display: grid;
        align-items: center;
        grid-template-areas: "i t t t t t t t ." "i a a a a a a a .";

        @media (max-width: 350px) {
          grid-column-gap: 6px;
        }

        img {
          grid-area: i;
          border-radius: 5px;
          width: 50px;
          height: 50px;
          object-fit: cover;

          @media (max-width: 1100px) {
            width: 35px;
            height: 35px;
            margin-right: 2px;
          }
        }
        .title {
          grid-area: t;
          margin: 0;
          font-size: 1em;
          padding-bottom: 2px;
          color: #4b4b4b;
          @media (max-width: 1100px) {
            font-size: .9em;
          }
        }
        .artist {
          grid-area: a;
          margin: 0;
          font-size: .85em;
          color: #9a9a9a;
        }
      }

      .album, .duration {
        font-size: .88em;
        color: grey;
      }
      .duration {
        padding-left: 3px;
        @media (max-width: 350px) {
          display: none;
        }
      }

      .heart, .dots {
        top: 50%;
        transform: translateY(-50%);
        color: #bfbfbf;
        opacity: .9;
        transition: .3s;
        @media (max-width: 1100px) {
          display: none;
        }

        &:hover {
          opacity: 1;
          color: $sidebarPurple;
        }
      }

      .heart {
        position: absolute;
        right: -50px;
      }

      .dots {
        position: absolute;
        right: -90px;
      }
    }

    .active {
      background-color: $brighterPurple;
      border-radius: 5px;

      .track {
        .title, .artist {
          color: white;
        }
      }

      .album, .duration {
        color: #ffffff;
      }
    }
  }
}