@import '../../assets/css/variables';

.main-layout {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: 1.2fr 6fr;

  @media (max-width: 1100px) {
    grid-template-columns: 1fr;
  }

  .sidebar-container {
    height: 100%;

    @media (max-width: 1100px) {
      height: 80px;
    }
  }

  main {
    display: grid;
    grid-template-rows: 230px minmax(90vh, auto);

    @media (max-width: 1100px) {
      grid-template-rows: auto auto;
    }

    header {
      width: 100%;
    }

    section {
      background-color: $bannerCreamy;
    }
  }

  .footer-container {
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
  }

  .minimized-banner {
    grid-template-rows: 150px auto;
  }

}